import { useForm } from "react-hook-form"
import { useNavigate } from "react-router-dom"
import { useSelector } from "react-redux"
import backDB from "../appwrite/backDB"
import { useEffect, useCallback } from "react"
import Button from "./Button";
import Input from "./Input"
import RTE from './RTE'
import Select from "./Select"

function PostForm({post}) {

    const {register, handleSubmit , watch , setValue, control , getValues} = useForm(
        {
            defaultValues:{
                title: post?.title || "",
                slug: post?.$id || "",
                content: post?.content || "",
                status: post?.status || "active",
            }
        }
    )

    const navigate = useNavigate();
    const userData = useSelector((state)=>state.authSlice.userData.userAccount);

    const submit = async (data)=>{
        if (post) {
            const img = data.image[0] ? await backDB.uploadFile(data.image[0]) : null;
            if (img) {
                await backDB.deleteFile(post.featuredImage);
            }

            const post = await backDB.updatePost(post.$id,
                {
                    ...data,
                    featuredImage : img ? img.$id : undefined,
                }
            )
            if (post) {
                navigate(`/post/${post.$id}`);
            }
            
        } else {
            const img = await backDB.uploadFile(data.image[0]);

            if(img)
            {
                data.featuredImage = img.$id;
                const dbPost = await backDB.createPost({...data,userId:userData.$id,userName:userData.name});
                
                if (dbPost) {
                    navigate(`/post/${dbPost.$id}`);
                }

            }
        }
    }

    const slugTransform = useCallback((value) => {
        if (value && typeof value === "string")
            return value
                .trim()
                .toLowerCase()
                .replace(/[^a-zA-Z\d\s]+/g, "-")
                .replace(/\s/g, "-");

        return "";
    }, []);

    useEffect(()=>{
        const subscription = watch((value, { name }) => {
            if (name === "title") {
                setValue("slug", slugTransform(value.title), { shouldValidate: true });
            }
        });

        return () => subscription.unsubscribe();
    },[watch, slugTransform, setValue]);

  return (
    <>
     <form onSubmit={handleSubmit(submit)} className="flex flex-wrap">
            <div className="w-2/3 px-2">
                <Input
                    label="Title :"
                    placeholder="Title"
                    className="mb-4"
                    {...register("title", { required: true })}
                />
                <Input
                    label="Slug :"
                    placeholder="Slug"
                    className="mb-4"
                    {...register("slug", { required: true })}
                    onInput={(e) => {
                        setValue("slug", slugTransform(e.currentTarget.value), { shouldValidate: true });
                    }}
                />
             <RTE label="Content :" name="content" control={control} defaultValue={getValues("content")} />
            </div>
            <div className="w-1/3 px-2">
                <Input
                    label="Featured Image :"
                    type="file"
                    className="mb-4"
                    accept="image/png, image/jpg, image/jpeg, image/gif"
                    {...register("image", { required: !post })}
                />
                {post && (
                    <div className="w-full mb-4">
                        <img
                            src={backDB.getFilePreview(post.featuredImage)}
                            alt={post.title}
                            className="rounded-lg"
                        />
                    </div>
                )}
                <Select
                    options={["active", "inactive"]}
                    label="Status"
                    className="mb-4"
                    {...register("status", { required: true })}
                />
                <Button type="submit" bgcolor={post ? "bg-green-500" : undefined} className="w-full">
                    {post ? "Update" : "Submit"}
                </Button>
            </div>
        </form>
    </>
  )
}

export default PostForm